// React
// import { useState } from "react";

// Material
import {
	Dialog,
	DialogTitle,
	DialogContent,
	DialogContentText,
	DialogActions,
	TextField,
	Button,
	Stack
} from "@mui/material";
import Chip from "@mui/material/Chip";

function ExerciseDialog({ open, onClose, exercise }) {
	return (
		<Dialog open={open} onClose={onClose} fullWidth>
			<DialogTitle>Edit this exercise</DialogTitle>
			<DialogContent>
				<DialogContentText>Hi</DialogContentText>
				<Stack direction='column' spacing={2}>
					<TextField
						// autoFocus
						margin='dense'
						id='exercise-name'
						label='Exercise Name'
						fullWidth
						variant='standard'
					/>
					<Stack direction='row' spacing={2}>
						{exercise?.categories.map((c, idx) => (
							<Chip onDelete={onClose} label={c} key={idx} />
						))}
					</Stack>
				</Stack>
			</DialogContent>
			<DialogActions>
				<Button onClick={onClose}>Cancel</Button>
				<Button onClick={onClose}>Save</Button>
			</DialogActions>
		</Dialog>
	);
}

export default ExerciseDialog;
