// React
import { useState } from "react";

// Components
import ExerciseDialog from "../components/MyExercises/ExerciseDialog";

// Material
import Paper from "@mui/material/Paper";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemText from "@mui/material/ListItemText";
import {
	IconButton,
	Typography,
	Stack,
	Accordion,
	AccordionSummary,
	AccordionDetails,
	TextField
} from "@mui/material";
import DeleteIcon from "@mui/icons-material/Delete";
import KeyboardArrowDownIcon from "@mui/icons-material/KeyboardArrowDown";
import EditIcon from "@mui/icons-material/Edit";

function MyExercisesPage({ user }) {
	const [expanded, setExpanded] = useState(true);
	const [searchedExercise, setSearchedExercise] = useState("");
	const [openDialog, setOpenDialog] = useState(false);
	const [selectedExerciseToEdit, setSelectedExerciseToEdit] = useState(null);

	/**
	 * Handles searching for exercise.
	 * @param {Event} e the TextField's event.
	 */
	function handleSearchedExercise(e) {
		setSearchedExercise(e.target.value);
	}

	/**
	 * Handles opening dialog to edit exercise.
	 * @param {Exercise} exercise the exercise to edit.
	 */
	function handleEditExercise(exercise) {
		setSelectedExerciseToEdit(exercise);
		setOpenDialog(true);
	}

	/**
	 * Handles closing dialog.
	 */
	function handleCloseDialog() {
		setOpenDialog(false);
	}

	return (
		<>
			<ExerciseDialog
				open={openDialog}
				onClose={handleCloseDialog}
				exercise={selectedExerciseToEdit}
			/>
			<Stack
				direction='column'
				sx={{
					margin: "100px 0"
				}}
			>
				<Paper variant='outlined'>
					<Accordion
						disableGutters
						expanded={expanded}
						onChange={() => setExpanded((expanded) => !expanded)}
						sx={{
							width: "350px",
							boxShadow: "none"
						}}
					>
						<AccordionSummary
							sx={{
								background: "#fefefe"
							}}
						>
							<Stack
								direction='row'
								alignItems='center'
								spacing={2}
							>
								<IconButton size='small'>
									<KeyboardArrowDownIcon
										sx={{
											transform: `rotate(${
												expanded ? "0deg" : "180deg"
											})`,
											transition: "0.2s ease-in-out"
										}}
									/>
								</IconButton>
								<Typography variant='h6'>
									My Exercises
								</Typography>
							</Stack>
						</AccordionSummary>
						<AccordionDetails
							sx={{
								padding: 0
							}}
						>
							<TextField
								label='Search for exercise'
								variant='filled'
								fullWidth
								value={searchedExercise}
								onChange={handleSearchedExercise}
							/>
							<List
								sx={{
									width: "100%",
									padding: "0"
								}}
							>
								{user.exercises
									.sort((a, b) => {
										if (a.name < b.name) {
											return -1;
										} else if (a.name > b.name) {
											return 1;
										}
										return 0;
									})
									.filter((exercise) =>
										exercise.name
											.toLowerCase()
											.includes(
												searchedExercise.toLowerCase()
											)
									)
									.map((exercise, idx) => (
										<ListItem
											divider
											key={idx}
											secondaryAction={
												<Stack
													direction='row'
													alignItems='center'
													spacing={1}
												>
													<IconButton
														edge='end'
														aria-label='edit'
														// To edit exercise
														onClick={() =>
															handleEditExercise(
																exercise
															)
														}
													>
														<EditIcon />
													</IconButton>
													<IconButton
														edge='end'
														aria-label='delete'
														// To delete exercise
														onClick={() =>
															alert(
																`Deleting ${exercise.name}`
															)
														}
													>
														<DeleteIcon />
													</IconButton>
												</Stack>
											}
										>
											<ListItemText
												primary={exercise.name}
											/>
										</ListItem>
									))}
							</List>
						</AccordionDetails>
					</Accordion>
				</Paper>
			</Stack>
		</>
	);
}

export default MyExercisesPage;
